import React, { useState } from 'react';
import { Avatar, Typography, Space, Button, Tooltip, message as antMessage } from 'antd';
import {
  UserOutlined,
  RobotOutlined,
  InfoCircleOutlined,
  CopyOutlined,
  ReloadOutlined,
  DatabaseOutlined,
} from '@ant-design/icons';
import { ChatMessage as ChatMessageType, ToolResultPayload } from '@/types';
import { useChatStore } from '@store/chat';
import ReactMarkdown from 'markdown-to-jsx';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow } from 'react-syntax-highlighter/dist/esm/styles/prism';
import ToolResultCard from './ToolResultCard';

const { Text } = Typography;

interface ChatMessageProps {
  message: ChatMessageType;
}

const ChatMessage: React.FC<ChatMessageProps> = ({ message }) => {
  const { type, content, timestamp, metadata } = message;
  const { saveMessageAsMemory } = useChatStore();
  const [isSaving, setIsSaving] = useState(false);

  // 
  const handleCopy = () => {
    navigator.clipboard.writeText(content);
  };

  // 
  const handleSaveAsMemory = async () => {
    try {
      setIsSaving(true);
      await saveMessageAsMemory(message);
      antMessage.success(' ');
    } catch (error) {
      console.error(':', error);
      antMessage.error(' ');
    } finally {
      setIsSaving(false);
    }
  };

  // 
  const formatTime = (date: Date) => {
    return new Date(date).toLocaleTimeString('zh-CN', {
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  // 
  const renderAvatar = () => {
    const avatarProps = {
      size: 32 as const,
      style: { flexShrink: 0 },
    };

    switch (type) {
      case 'user':
        return (
          <Avatar 
            {...avatarProps}
            icon={<UserOutlined />}
            style={{ ...avatarProps.style, backgroundColor: '#1890ff' }}
          />
        );
      case 'assistant':
        return (
          <Avatar 
            {...avatarProps}
            icon={<RobotOutlined />}
            style={{ ...avatarProps.style, backgroundColor: '#52c41a' }}
          />
        );
      case 'system':
        return (
          <Avatar 
            {...avatarProps}
            icon={<InfoCircleOutlined />}
            style={{ ...avatarProps.style, backgroundColor: '#faad14' }}
          />
        );
      default:
        return null;
    }
  };

  // 
  const CodeBlock = ({ children, className }: { children: string; className?: string }) => {
    const language = className?.replace('lang-', '') || 'text';
    
    return (
      <SyntaxHighlighter
        style={tomorrow}
        language={language}
        PreTag="div"
        customStyle={{
          margin: '8px 0',
          borderRadius: '6px',
          fontSize: '13px',
        }}
      >
        {children}
      </SyntaxHighlighter>
    );
  };

  // 
  const renderContent = () => {
    if (type === 'system') {
      return (
        <Text type="secondary" style={{ fontStyle: 'italic' }}>
          {content}
        </Text>
      );
    }

    return (
      <ReactMarkdown
        options={{
          overrides: {
            code: {
              component: CodeBlock,
            },
            pre: {
              component: ({ children }: { children: React.ReactNode }) => (
                <div>{children}</div>
              ),
            },
            p: {
              props: {
                style: { margin: '0 0 8px 0', lineHeight: 1.6 },
              },
            },
            ul: {
              props: {
                style: { margin: '8px 0', paddingLeft: '20px' },
              },
            },
            ol: {
              props: {
                style: { margin: '8px 0', paddingLeft: '20px' },
              },
            },
            blockquote: {
              props: {
                style: {
                  borderLeft: '4px solid #d9d9d9',
                  paddingLeft: '12px',
                  margin: '8px 0',
                  color: '#666',
                  fontStyle: 'italic',
                },
              },
            },
          },
        }}
      >
        {content}
      </ReactMarkdown>
    );
  };

  // 
  const renderMetadata = () => {
    if (!metadata) return null;

    const planTitle = metadata.plan_title;
    const planId = metadata.plan_id;
    if (!planTitle && (planId === undefined || planId === null)) {
      return null;
    }

    return (
      <div style={{ marginTop: 8, fontSize: 12, color: '#999' }}>
        <div>
          :
          {planTitle ? ` ${planTitle}` : ''}
          {planId !== undefined && planId !== null ? ` (#${planId})` : ''}
        </div>
      </div>
    );
  };

  const renderToolResults = () => {
    const toolResults = Array.isArray(metadata?.tool_results)
      ? (metadata?.tool_results as ToolResultPayload[])
      : [];
    if (!toolResults.length) {
      return null;
    }
    return (
      <Space direction="vertical" size="middle" style={{ width: '100%', marginTop: 12 }}>
        {toolResults.map((result, index) => (
          <ToolResultCard key={`${result.name ?? 'tool'}_${index}`} payload={result} defaultOpen={index === 0} />
        ))}
      </Space>
    );
  };

  return (
    <div className={`message ${type}`}>
      {renderAvatar()}
      
      <div className="message-content">
        <div className="message-bubble">
          {renderContent()}
          {renderToolResults()}
          {renderMetadata()}
        </div>
        
        <div className="message-time">
          <Space size="small">
            <Text type="secondary" style={{ fontSize: 12 }}>
              {formatTime(timestamp)}
            </Text>
            
            {type !== 'system' && (
              <Space size={4}>
                <Tooltip title="">
                  <Button
                    type="text"
                    size="small"
                    icon={<CopyOutlined />}
                    onClick={handleCopy}
                    style={{ fontSize: 10, padding: '0 4px' }}
                  />
                </Tooltip>

                <Tooltip title="">
                  <Button
                    type="text"
                    size="small"
                    icon={<DatabaseOutlined />}
                    onClick={handleSaveAsMemory}
                    loading={isSaving}
                    style={{ fontSize: 10, padding: '0 4px' }}
                  />
                </Tooltip>

                {type === 'assistant' && (
                  <Tooltip title="">
                    <Button
                      type="text"
                      size="small"
                      icon={<ReloadOutlined />}
                      style={{ fontSize: 10, padding: '0 4px' }}
                    />
                  </Tooltip>
                )}
              </Space>
            )}
          </Space>
        </div>
      </div>
    </div>
  );
};

export default ChatMessage;
