import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { Row, Col, Card, Table, Tag, Button, Space, Input, Select, message, Tooltip, Popconfirm, Statistic, Progress, Tabs, Alert, Empty } from 'antd';
import {
  PlusOutlined,
  SearchOutlined,
  FilterOutlined,
  DeleteOutlined,
  EyeOutlined,
  ReloadOutlined,
  DatabaseOutlined,
  UnorderedListOutlined,
  ApartmentOutlined,
  ExclamationCircleOutlined,
  CloseCircleOutlined,
} from '@ant-design/icons';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { memoryApi } from '@api/memory';
import { useMemoryStore } from '@store/memory';
import SaveMemoryModal from '@components/memory/SaveMemoryModal';
import MemoryDetailDrawer from '@components/memory/MemoryDetailDrawer';
import MemoryGraph from '@components/memory/MemoryGraph';
import type { Memory } from '@/types';
import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import 'dayjs/locale/zh-cn';

dayjs.extend(relativeTime);
dayjs.locale('zh-cn');

const { Search } = Input;

// hook - 
const useDebounce = <T,>(value: T, delay: number): T => {
  const [debouncedValue, setDebouncedValue] = useState<T>(value);

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);

  return debouncedValue;
};

const MemoryPage: React.FC = () => {
  const queryClient = useQueryClient();
  const {
    memories,
    filters,
    setMemories,
    setFilters,
    clearFilters,
    removeMemory,
    setSelectedMemory,
    setStats,
    getFilteredMemories,
  } = useMemoryStore();

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isDetailDrawerOpen, setIsDetailDrawerOpen] = useState(false);
  const [activeTab, setActiveTab] = useState('list');
  const [searchInput, setSearchInput] = useState(filters.search_query);

  //  - 500ms
  const debouncedSearchQuery = useDebounce(searchInput, 500);

  // store
  useEffect(() => {
    if (debouncedSearchQuery !== filters.search_query) {
      setFilters({ search_query: debouncedSearchQuery });
    }
  }, [debouncedSearchQuery]);

  // 
  const { data: stats, error: statsError, isError: isStatsError } = useQuery({
    queryKey: ['memory-stats'],
    queryFn: () => memoryApi.getStats(),
    refetchInterval: 30000,
    retry: 2,
    retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 5000),
    onError: (error: any) => {
      message.error(`: ${error.message || ''}`);
    },
  });

  useEffect(() => {
    if (stats) {
      setStats(stats);
    }
  }, [stats, setStats]);

  // 
  const {
    data: queryResult,
    isLoading,
    error: queryError,
    isError: isQueryError,
    refetch
  } = useQuery({
    queryKey: ['memories', filters],
    queryFn: () => memoryApi.queryMemory({
      search_text: filters.search_query || ' ',
      memory_types: filters.memory_types as any[],
      importance_levels: filters.importance_levels as any[],
      limit: 100,
      min_similarity: filters.min_similarity,
    }),
    refetchInterval: 30000,
    retry: 2,
    retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 5000),
    onError: (error: any) => {
      message.error(`: ${error.message || ''}`);
    },
  });

  useEffect(() => {
    if (queryResult?.memories) {
      setMemories(queryResult.memories);
    }
  }, [queryResult, setMemories]);

  // 
  const handleRefresh = useCallback(async () => {
    try {
      await refetch();
      message.success('');
    } catch (error: any) {
      message.error(`: ${error.message || ''}`);
    }
  }, [refetch]);

  // 
  const handleSearchChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchInput(e.target.value);
  }, []);

  // 
  const handleSearchSubmit = useCallback(() => {
    setFilters({ search_query: searchInput });
    refetch();
  }, [searchInput, setFilters, refetch]);

  // 
  const handleClearFilters = useCallback(() => {
    setSearchInput('');
    clearFilters();
    refetch();
  }, [clearFilters, refetch]);

  // 
  const getMemoryTypeColor = (type: Memory['memory_type']) => {
    const colors = {
      conversation: 'blue',
      experience: 'green',
      knowledge: 'purple',
      context: 'orange',
    };
    return colors[type] || 'default';
  };

  // 
  const getImportanceColor = (importance: Memory['importance']) => {
    const colors = {
      critical: 'red',
      high: 'orange',
      medium: 'blue',
      low: 'default',
      temporary: 'gray',
    };
    return colors[importance] || 'default';
  };

  // 
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
      width: 100,
      ellipsis: true,
      render: (id: string) => (
        <Tooltip title={id}>
          <span style={{ fontFamily: 'monospace', fontSize: '12px' }}>
            {id.substring(0, 8)}...
          </span>
        </Tooltip>
      ),
    },
    {
      title: '',
      dataIndex: 'content',
      key: 'content',
      ellipsis: true,
      render: (content: string) => (
        <Tooltip title={content}>
          <span>{content.length > 100 ? content.substring(0, 100) + '...' : content}</span>
        </Tooltip>
      ),
    },
    {
      title: '',
      dataIndex: 'memory_type',
      key: 'memory_type',
      width: 120,
      filters: [
        { text: '', value: 'conversation' },
        { text: '', value: 'experience' },
        { text: '', value: 'knowledge' },
        { text: '', value: 'context' },
      ],
      onFilter: (value: any, record: Memory) => record.memory_type === value,
      render: (type: Memory['memory_type']) => {
        const labels = {
          conversation: '',
          experience: '',
          knowledge: '',
          context: '',
        };
        return <Tag color={getMemoryTypeColor(type)}>{labels[type]}</Tag>;
      },
    },
    {
      title: '',
      dataIndex: 'importance',
      key: 'importance',
      width: 100,
      filters: [
        { text: '', value: 'critical' },
        { text: '', value: 'high' },
        { text: '', value: 'medium' },
        { text: '', value: 'low' },
        { text: '', value: 'temporary' },
      ],
      onFilter: (value: any, record: Memory) => record.importance === value,
      render: (importance: Memory['importance']) => {
        const labels = {
          critical: '',
          high: '',
          medium: '',
          low: '',
          temporary: '',
        };
        return <Tag color={getImportanceColor(importance)}>{labels[importance]}</Tag>;
      },
    },
    {
      title: '',
      dataIndex: 'keywords',
      key: 'keywords',
      width: 200,
      render: (keywords: string[]) => (
        <Space size={[0, 4]} wrap>
          {keywords.slice(0, 3).map((keyword, index) => (
            <Tag key={index} style={{ fontSize: '11px' }}>{keyword}</Tag>
          ))}
          {keywords.length > 3 && <Tag>+{keywords.length - 3}</Tag>}
        </Space>
      ),
    },
    {
      title: '',
      dataIndex: 'similarity',
      key: 'similarity',
      width: 100,
      render: (similarity?: number) => similarity !== undefined ? (
        <Tag color={similarity > 0.8 ? 'green' : similarity > 0.6 ? 'blue' : 'default'}>
          {(similarity * 100).toFixed(1)}%
        </Tag>
      ) : '-',
      sorter: (a: Memory, b: Memory) => (a.similarity || 0) - (b.similarity || 0),
    },
    {
      title: '',
      dataIndex: 'retrieval_count',
      key: 'retrieval_count',
      width: 100,
      sorter: (a: Memory, b: Memory) => a.retrieval_count - b.retrieval_count,
    },
    {
      title: '',
      dataIndex: 'created_at',
      key: 'created_at',
      width: 160,
      render: (created_at: string) => (
        <Tooltip title={dayjs(created_at).format('YYYY-MM-DD HH:mm:ss')}>
          {dayjs(created_at).fromNow()}
        </Tooltip>
      ),
      sorter: (a: Memory, b: Memory) => dayjs(a.created_at).unix() - dayjs(b.created_at).unix(),
    },
    {
      title: '',
      key: 'action',
      width: 120,
      fixed: 'right' as const,
      render: (_: any, record: Memory) => (
        <Space size="small">
          <Tooltip title="">
            <Button
              type="text"
              size="small"
              icon={<EyeOutlined />}
              onClick={() => {
                setSelectedMemory(record);
                setIsDetailDrawerOpen(true);
              }}
            />
          </Tooltip>
        </Space>
      ),
    },
  ];

  return (
    <div>
      {/*  */}
      <div className="content-header">
        <h2 style={{ margin: 0, display: 'flex', alignItems: 'center', gap: '8px' }}>
          <DatabaseOutlined style={{ color: '#1890ff' }} />
          
        </h2>
        <p style={{ margin: '8px 0 0 0', color: '#666' }}>
          Memory-MCP  - AI
        </p>
      </div>

      <div className="content-body">
        {/*  -  */}
        {isStatsError && (
          <Alert
            message=""
            description={`: ${(statsError as any)?.message || ''}`}
            type="error"
            showIcon
            closable
            icon={<CloseCircleOutlined />}
            style={{ marginBottom: 16 }}
            action={
              <Button size="small" danger onClick={handleRefresh}>
                
              </Button>
            }
          />
        )}

        {/*  -  */}
        {isQueryError && (
          <Alert
            message=""
            description={`: ${(queryError as any)?.message || ''}`}
            type="error"
            showIcon
            closable
            icon={<CloseCircleOutlined />}
            style={{ marginBottom: 16 }}
            action={
              <Button size="small" danger onClick={handleRefresh}>
                
              </Button>
            }
          />
        )}

        {/*  */}
        {stats && !isStatsError && (
          <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
            <Col xs={24} sm={12} md={6}>
              <Card>
                <Statistic
                  title=""
                  value={stats.total_memories}
                  prefix={<DatabaseOutlined />}
                  valueStyle={{ color: '#1890ff' }}
                />
              </Card>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Card>
                <Statistic
                  title=""
                  value={stats.average_connections}
                  precision={1}
                  valueStyle={{ color: '#52c41a' }}
                  suffix=""
                />
              </Card>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Card>
                <Statistic
                  title=""
                  value={(stats.embedding_coverage * 100).toFixed(1)}
                  valueStyle={{ color: stats.embedding_coverage > 0.8 ? '#52c41a' : '#faad14' }}
                  suffix="%"
                />
                <Progress
                  percent={stats.embedding_coverage * 100}
                  size="small"
                  showInfo={false}
                  style={{ marginTop: 8 }}
                />
              </Card>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Card>
                <Statistic
                  title=""
                  value={stats.evolution_count || 0}
                  valueStyle={{ color: '#722ed1' }}
                />
              </Card>
            </Col>
          </Row>
        )}

        {/*  */}
        <Card style={{ marginBottom: 16 }}>
          <Space wrap style={{ width: '100%', justifyContent: 'space-between' }}>
            <Space wrap>
              <Search
                placeholder="..."
                allowClear
                style={{ width: 300 }}
                value={searchInput}
                onChange={handleSearchChange}
                onSearch={handleSearchSubmit}
                prefix={<SearchOutlined />}
                loading={isLoading}
              />

              <Select
                mode="multiple"
                placeholder=""
                style={{ minWidth: 180 }}
                value={filters.memory_types}
                onChange={(value) => setFilters({ memory_types: value })}
                options={[
                  { label: '', value: 'conversation' },
                  { label: '', value: 'experience' },
                  { label: '', value: 'knowledge' },
                  { label: '', value: 'context' },
                ]}
                maxTagCount="responsive"
                disabled={isLoading}
              />

              <Select
                mode="multiple"
                placeholder=""
                style={{ minWidth: 150 }}
                value={filters.importance_levels}
                onChange={(value) => setFilters({ importance_levels: value })}
                options={[
                  { label: '', value: 'critical' },
                  { label: '', value: 'high' },
                  { label: '', value: 'medium' },
                  { label: '', value: 'low' },
                  { label: '', value: 'temporary' },
                ]}
                maxTagCount="responsive"
                disabled={isLoading}
              />

              <Button
                icon={<FilterOutlined />}
                onClick={handleClearFilters}
                disabled={isLoading}
              >
                
              </Button>
            </Space>

            <Space>
              <Button
                icon={<ReloadOutlined />}
                onClick={handleRefresh}
                loading={isLoading}
              >
                
              </Button>
              <Button
                type="primary"
                icon={<PlusOutlined />}
                onClick={() => setIsModalOpen(true)}
              >
                
              </Button>
            </Space>
          </Space>
        </Card>

        {/*  */}
        <Card>
          <Tabs
            activeKey={activeTab}
            onChange={setActiveTab}
            items={[
              {
                key: 'list',
                label: (
                  <span>
                    <UnorderedListOutlined />
                    
                  </span>
                ),
                children: (
                  <Table
                    columns={columns}
                    dataSource={getFilteredMemories()}
                    rowKey="id"
                    loading={isLoading}
                    pagination={{
                      showSizeChanger: true,
                      showQuickJumper: true,
                      showTotal: (total) => ` ${total} `,
                      defaultPageSize: 20,
                      pageSizeOptions: ['10', '20', '50', '100'],
                    }}
                    scroll={{ x: 1400 }}
                  />
                ),
              },
              {
                key: 'graph',
                label: (
                  <span>
                    <ApartmentOutlined />
                    
                  </span>
                ),
                children: (
                  <MemoryGraph
                    onNodeClick={(memory) => {
                      setSelectedMemory(memory);
                      setIsDetailDrawerOpen(true);
                    }}
                    height="calc(100vh - 450px)"
                  />
                ),
              },
            ]}
          />
        </Card>
      </div>

      {/* Modal */}
      <SaveMemoryModal
        open={isModalOpen}
        onCancel={() => setIsModalOpen(false)}
      />

      {/* Drawer */}
      <MemoryDetailDrawer
        open={isDetailDrawerOpen}
        onClose={() => setIsDetailDrawerOpen(false)}
      />
    </div>
  );
};

export default MemoryPage;
