import React, { useEffect } from 'react';
import { Modal, Form, Input, Select, Tag, Space, message } from 'antd';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { memoryApi } from '@api/memory';
import { useMemoryStore } from '@store/memory';
import type { SaveMemoryRequest } from '@/types';

const { TextArea } = Input;
const { Option } = Select;

interface SaveMemoryModalProps {
  open: boolean;
  onCancel: () => void;
}

const SaveMemoryModal: React.FC<SaveMemoryModalProps> = ({ open, onCancel }) => {
  const [form] = Form.useForm();
  const queryClient = useQueryClient();
  const { addMemory } = useMemoryStore();

  // Mutation
  const saveMutation = useMutation({
    mutationFn: (values: SaveMemoryRequest) => memoryApi.saveMemory(values),
    onSuccess: (newMemory) => {
      message.success(' !');
      addMemory(newMemory);
      queryClient.invalidateQueries(['memories']);
      queryClient.invalidateQueries(['memory-stats']);
      form.resetFields();
      onCancel();
    },
    onError: (error: any) => {
      message.error(` : ${error.message}`);
    },
  });

  // Modal
  useEffect(() => {
    if (!open) {
      form.resetFields();
    }
  }, [open, form]);

  const handleSubmit = async () => {
    try {
      const values = await form.validateFields();

      //  ()
      const formattedValues: SaveMemoryRequest = {
        ...values,
        tags: values.tags || [],
        keywords: values.keywords || [],
      };

      saveMutation.mutate(formattedValues);
    } catch (error) {
      console.error(':', error);
    }
  };

  return (
    <Modal
      title=" "
      open={open}
      onOk={handleSubmit}
      onCancel={onCancel}
      confirmLoading={saveMutation.isPending}
      width={700}
      okText=""
      cancelText=""
      destroyOnClose
    >
      <Form
        form={form}
        layout="vertical"
        initialValues={{
          memory_type: 'experience',
          importance: 'medium',
          context: 'General',
        }}
      >
        {/*  */}
        <Form.Item
          label=""
          name="content"
          rules={[
            { required: true, message: '' },
            { min: 10, message: '10' },
            { max: 5000, message: '5000' },
          ]}
        >
          <TextArea
            placeholder="..."
            rows={6}
            showCount
            maxLength={5000}
          />
        </Form.Item>

        {/*  */}
        <Form.Item
          label=""
          name="memory_type"
          rules={[{ required: true, message: '' }]}
        >
          <Select placeholder="">
            <Option value="conversation">
              <Space>
                <Tag color="blue"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="experience">
              <Space>
                <Tag color="green"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="knowledge">
              <Space>
                <Tag color="purple"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="context">
              <Space>
                <Tag color="orange"></Tag>
                <span></span>
              </Space>
            </Option>
          </Select>
        </Form.Item>

        {/*  */}
        <Form.Item
          label=""
          name="importance"
          rules={[{ required: true, message: '' }]}
        >
          <Select placeholder="">
            <Option value="critical">
              <Space>
                <Tag color="red"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="high">
              <Space>
                <Tag color="orange"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="medium">
              <Space>
                <Tag color="blue"></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="low">
              <Space>
                <Tag></Tag>
                <span></span>
              </Space>
            </Option>
            <Option value="temporary">
              <Space>
                <Tag color="gray"></Tag>
                <span></span>
              </Space>
            </Option>
          </Select>
        </Form.Item>

        {/*  */}
        <Form.Item
          label=""
          name="tags"
          tooltip=","
        >
          <Select
            mode="tags"
            placeholder="..."
            tokenSeparators={[',']}
            maxTagCount="responsive"
            style={{ width: '100%' }}
          />
        </Form.Item>

        {/*  */}
        <Form.Item
          label=""
          name="keywords"
          tooltip=","
        >
          <Select
            mode="tags"
            placeholder="..."
            tokenSeparators={[',']}
            maxTagCount="responsive"
            style={{ width: '100%' }}
          />
        </Form.Item>

        {/*  */}
        <Form.Item
          label=""
          name="context"
          tooltip=""
        >
          <Input placeholder=": " />
        </Form.Item>

        {/* ID () */}
        <Form.Item
          label="ID"
          name="related_task_id"
          tooltip=",ID"
        >
          <Input
            type="number"
            placeholder="ID ()"
            min={1}
          />
        </Form.Item>
      </Form>

      {/*  */}
      <div style={{
        marginTop: '16px',
        padding: '12px',
        background: '#f0f5ff',
        borderRadius: '6px',
        fontSize: '13px',
        color: '#666'
      }}>
        <div> <strong>:</strong></div>
        <ul style={{ marginTop: '8px', marginBottom: 0, paddingLeft: '20px' }}>
          <li>,</li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </Modal>
  );
};

export default SaveMemoryModal;
